@keyframes sgbp-zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.1, 0.1, 0.1); }
  50% {
    opacity: 1;
    transform: scale3d(1, 1, 1); } }

@keyframes sgbp-fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes sgbp-slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    transform: translate3d(0, 0, 0); } }

@keyframes sgbp-slideOutDown {
  from {
    transform: translate3d(0, 0, 0); }
  to {
    visibility: hidden;
    transform: translate3d(0, 100%, 0); } }

@keyframes sgbp-bounceInDown {
  from,
  60%,
  75%,
  90%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    transform: translate3d(0, -3000px, 0); }
  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0); }
  75% {
    transform: translate3d(0, -10px, 0); }
  90% {
    transform: translate3d(0, 5px, 0); }
  to {
    transform: none; } }

@keyframes sgbp-ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.5; }
  20% {
    transform: scale(15, 15);
    opacity: 0.5; }
  100% {
    opacity: 0;
    transform: scale(40, 40); } }

.sgbp-animated {
  animation-fill-mode: both;
  animation-duration: 400ms; }
  .sgbp-animated--zoomIn {
    animation-name: sgbp-zoomIn; }
  .sgbp-animated--fadeIn {
    animation-name: sgbp-fadeIn; }
  .sgbp-animated--slideInUp {
    animation-name: sgbp-slideInUp; }
  .sgbp-animated--slideOutDown {
    animation-name: sgbp-slideOutDown; }
  .sgbp-animated--bounceInDown {
    animation-name: sgbp-bounceInDown; }
  .sgbp-animated--rippleError, .sgbp-animated--rippleSuccess {
    position: relative;
    overflow: hidden; }
    .sgbp-animated--rippleError::after, .sgbp-animated--rippleSuccess::after {
      animation: sgbp-ripple 800ms ease-out;
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 5%;
      height: 5%;
      opacity: 0;
      transform: scale(1, 1) translate(-50%);
      transform-origin: 50% 50%; }
  .sgbp-animated--rippleError::after {
    background: var(--sgbp-color-error); }
  .sgbp-animated--rippleSuccess::after {
    background: var(--sgbp-color-success); }

:root {
  --sgbp-tooltip-z-index: 9999;
  --sgbp-tooltip-background-color: var(--sgbp-color-gray-lighter);
  --sgbp-tooltip-color: var(--sgbp-color-black);
  --sgbp-tooltip-box-shadow-color: var(--sgbp-color-darken);
  --sgbp-tooltip-padding: var(--sgbp-default-margin); }

.sgbp-tooltip {
  display: inline-block;
  position: relative; }

.sgbp-tooltip--block {
  display: block; }

.sgbp-tooltip__content {
  position: absolute;
  z-index: var(--sgbp-tooltip-z-index);
  display: none;
  top: 0;
  max-width: 100vw; }

.sgbp-tooltip--styled .sgbp-tooltip__content {
  box-shadow: 2px 2px 15px -2px var(--sgbp-tooltip-box-shadow-color);
  padding: var(--sgbp-tooltip-padding);
  background-color: var(--sgbp-tooltip-background-color);
  color: var(--sgbp-tooltip-color);
  font-weight: normal;
  white-space: normal; }

